"use client";

import React, { useState } from "react";
import Image from "next/image";
import LuxuryImg from "@/public/images/property/facilities2.jpg";
import PdfImg from "@/public/images/property/pdf.png";
import WhatsappImg from "@/public/images/property/whatsapp.png";
import { FacilitiesTabs, ResidentialLayoutTabs } from "./enum";

export default function Facilities() {
  const [activeTab, setActiveTab] = useState(0);

  return (
    <section>
      <div className="mx-auto max-w-6xl pb-10 bg-blue-200">
        <Image className="relative" src={LuxuryImg} alt="corporate_img" />

        <div
          className="flex flex-col gap-4  text-lg m-auto py-5 px-20"
          style={{ fontFamily: "ACaslonPro-Regular" }}
        >
          <p className="text-5xl w-[90%] tracking-wide text-amber-950">
            OVER 70,000 SQ FT OF LIFESTYLE FACILITIES
          </p>
          <p className="tracking-wide text-xl text-amber-950">
            Unwind and play in exceptional spaces
          </p>
          <p className="text-justify tracking-wide">
            Relax and enjoy the longest rooftop infinity pool at 118 meters,
            along with the 15,000 sq. ft. Sky Sports Zone at Pavilion Square –
            perfect for unwinding and having fun. On level 12, the function
            room, entertainment room, and BBQ deck are great for family and
            friend gatherings.
          </p>
          <div>
            <a
              className="btn bg-green-400 text-gray-800 shadow-sm hover:bg-green-100 mt-10"
              href="#0"
            >
              FIND OUT MORE
            </a>
          </div>
        </div>
      </div>
      {/* Facilities Plan */}
      <div className="mx-auto max-w-6xl px-20 py-10 bg-[#371809] flex flex-col gap-8">
        <div
          className="text-4xl text-center text-[#ff6602]"
          style={{ fontFamily: "ACaslonPro-Regular" }}
        >
          FACILITIES PLAN
        </div>
        <div className="flex justify-between bg-[#a4725ab3] rounded-2xl p-1">
          {FacilitiesTabs.map((tab, index) => (
            <button
              key={index}
              onClick={() => setActiveTab(index)}
              className={`flex-1 text-center px-4 py-2 rounded-full text-sm font-medium transition-all duration-300 cursor-pointer
              ${
                activeTab === index
                  ? "bg-white shadow text-black"
                  : " hover:text-black text-white hover:bg-white hover:opacity-50"
              }`}
            >
              Level {tab.level}
            </button>
          ))}
        </div>

        {/* Tab Content */}
        <div className="bg-white p-6 rounded-xl shadow">
          <h2 className="text-xl font-semibold text-gray-800 mb-2">
            <Image
              src={FacilitiesTabs[activeTab].image}
              alt={String(FacilitiesTabs[activeTab].level)}
            />
          </h2>
          <div
            className="text-center pb-5 text-lg text-black"
            style={{ fontFamily: "ACaslonPro-Regular" }}
          >
            {FacilitiesTabs[activeTab].description}
          </div>
          {/* description */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-2 text-center">
            {FacilitiesTabs[activeTab].item.map((item, index) => (
              <span key={index} className="flex gap-3 items-center">
                <span className="flex items-center justify-center w-8 h-8 rounded-full bg-amber-600 text-white font-bold">
                  {index + 1}
                </span>
                <span>{item}</span>
              </span>
            ))}
          </div>
        </div>

        <div className="text-center">
          <a className="btn bg-green-500 text-gray-800" href="#0">
            <Image src={PdfImg} width={45} alt="e-brochure" />
            <span className="text-lg text-white font-semibold">
              DOWNLOAD E-BROCHURE
            </span>
          </a>
        </div>
      </div>
      {/* Facilities Layout */}
      <div className="mx-auto max-w-6xl px-20 py-10 bg-orange-500 flex flex-col gap-8">
        <div
          className="text-4xl text-center text-[#371809]"
          style={{ fontFamily: "ACaslonPro-Regular" }}
        >
          RESIDENTIAL LAYOUT
        </div>
        <div className="flex justify-between bg-[#371809] rounded-2xl p-1">
          {ResidentialLayoutTabs.map((tab, index) => (
            <button
              key={index}
              onClick={() => setActiveTab(index)}
              className={`flex-1 text-center px-4 py-2 rounded-full text-sm font-medium transition-all duration-300 cursor-pointer
              ${
                activeTab === index
                  ? "bg-orange-200 shadow text-black"
                  : " hover:text-black text-white hover:bg-white hover:opacity-50"
              }`}
            >
              Type {tab.layout}
            </button>
          ))}
        </div>

        {/* Tab Content */}
        <div className="bg-white p-6 rounded-xl shadow">
          <h2 className="text-xl font-semibold text-gray-800 mb-2">
            <Image
              src={FacilitiesTabs[activeTab].image}
              alt={String(FacilitiesTabs[activeTab].level)}
            />
          </h2>
        </div>

        <div className="text-center">
          <a className="btn bg-green-500 text-gray-800" href="#0">
            <Image src={WhatsappImg} width={45} alt="e-brochure" />
            <span className="text-lg text-white font-semibold">
              WHATSAPP FOR LATEST PRICING
            </span>
          </a>
        </div>
      </div>
    </section>
  );
}
